import {Component, OnInit} from '@angular/core';
import {TranslateService} from "@ngx-translate/core";
import {Linguages} from "./models/linguages";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit{

  /**
   * Variable(s) qui pourront être utilisées dans toutes la classe
   */
  public name          : string;
  public firstName     : string;
  public langTranslate : string[];
  public linguages     : Linguages[];

  /**
   * Constructeur
   * @param translate
   */
  constructor(public translate: TranslateService) {
  }

  /**
   * Initialisation des variables au moment du chargement de la vue dans le navigateur
   */
  ngOnInit(): void {
    this.name          = 'Blois';
    this.firstName     = 'Loïc';
    this.langTranslate = ['en', 'fr'];
    this.linguages = [
      {value: 'english', viewValue  : 'English',  flag : 'assets/img/flags/united-kingdom-flag-round-icon-16.png' },
      {value: 'français', viewValue : 'Français', flag : 'assets/img/flags/france-flag-round-icon-16.png' }
    ];
    this.translate.setDefaultLang(this.langTranslate[0]); // language par défaut = 'en'

  }

  /**
   * Traduction effectuée sur la vue seulement
   * @param lang
   */
  public changeLinguage(lang: string) {
    switch (lang) {
      case 'english':
        return this.translate.use(this.langTranslate[0]);
        break;
      case 'français':
        return this.translate.use(this.langTranslate[1]);
        break;
    }
  }
}
